.appointmentPreviewItem {
    display: flex;
    flex-direction: column;
}

.header {
    display: flex;
    align-items: center;
}

.appointmentPreviewItemRow {
    display: flex;
    background: var(--gray-bg-color);
    padding: 9px 28px 9px 28px;
    margin-top: 8px;
    border-radius: 5px;
}

.appointmentPreviewItemRow:hover {
    cursor: pointer;
    background: var(--blue-bg-color);
}

.column {
    display: flex;
    flex-direction: column;
}
.firstColumn {
    flex: 1 0 200px;
    align-items: flex-start;
}

.title {
    font: var(--font-s);
    font-weight: 700;
    margin-left: 16px;
    margin-right: 32px;
}

.number {
    font: var(--font-s);
}

.subtitle {
    font: var(--font-s2);
    font-weight: 700;
    margin-bottom: 6px;
}

.tag {
    margin: 12px 0 0 0;
}

.verticalLine {
    display: block;
    width: 2px;
    background: var(--black-color);
    border-radius: 23px;
    margin:  0 25px;
}